import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: (resolve) => require(['@/page/index/index.vue'], resolve),
      children:[
          { name: 'Home', path: '/Home', meta: { title: '主页' }, component: (resolve) => require(['@/page/Home/Home.vue'],resolve) },
          { name: 'useHelp', path: '/useHelp', meta: { title: '问题解答' }, component: (resolve) => require(['@/page/useHelp/useHelp.vue'], resolve) },
          { name: 'ManualCode', path: '/ManualCode', meta: { title: '手动输码' }, component: (resolve) => require(['@/page/ManualCode/ManualCode.vue'], resolve) },
        { name: 'helpCenter', path: '/helpCenter', meta: { title: '帮助中心' }, component: (resolve) => require(['@/page/helpCenter/helpCenter.vue'], resolve) },
        { name: 'feedback', path: '/feedback', meta: { title: '意见反馈' }, component: (resolve) => require(['@/page/feedback/feedback.vue'], resolve) },
        { name: 'rentCar', path: '/rentCar', meta: { title: '商家如何创建门店' }, component: (resolve) => require(['@/page/rentCar/rentCar.vue'], resolve) },
        { name: 'addGoods', path: '/addGoods', meta: { title: '商家如何新增商品' }, component: (resolve) => require(['@/page/rentCar/addGoods.vue'], resolve) },
        { name: 'carll', path: '/carll', meta: { title: '商家如何录入车辆' }, component: (resolve) => require(['@/page/rentCar/carll.vue'], resolve) },
        { name: 'setMeal', path: '/setMeal', meta: { title: '商家如何购买套餐' }, component: (resolve) => require(['@/page/rentCar/setMeal.vue'], resolve) },
        { name: 'withdraw', path: '/withdraw', meta: { title: '商家如何提现' }, component: (resolve) => require(['@/page/rentCar/withdraw.vue'], resolve) },
        { name: 'getCar', path: '/getCar', meta: { title: '用户如何取车、还车' }, component: (resolve) => require(['@/page/rentCar/getCar.vue'], resolve) },
        { name: 'order', path: '/order', meta: { title: '商家如何查看订单' }, component: (resolve) => require(['@/page/rentCar/order.vue'], resolve) },
        { name: 'userMent', path: '/userMent', meta: { title: '商家如何管理用户' }, component: (resolve) => require(['@/page/rentCar/userMent.vue'], resolve) },
        { name: 'carMent', path: '/carMent', meta: { title: '商家如何管理车辆' }, component: (resolve) => require(['@/page/rentCar/carMent.vue'], resolve) },
        { name: 'operationaData', path: '/operationaData', meta: { title: '商家如何查看运营数据' }, component: (resolve) => require(['@/page/rentCar/operationaData.vue'], resolve) },
        { name: 'carEdit', path: '/carEdit', meta: { title: '商家如何更改车辆录入商品' }, component: (resolve) => require(['@/page/rentCar/carEdit.vue'], resolve) },
        { name: 'centerControl', path: '/centerControl', meta: { title: '商家如何更换中控？' }, component: (resolve) => require(['@/page/rentCar/centerControl.vue'], resolve) },
        { name: 'dataStat', path: '/dataStat', meta: { title: '数据统计' }, component: (resolve) => require(['@/page/dataStat/dataStat.vue'], resolve) },
        { name: 'feedbacksFlow', path: '/feedbacksFlow', meta: { title: "租车指南" }, component: (resolve) => require(['@/page/feedbacksFlow/feedbacksFlow.vue'], resolve) },
        { name: 'goodsDetails', path: '/goodsDetails', meta: { title: "商品详情租车流程" }, component: (resolve) => require(['@/page/goodsDetails/goodsDetails.vue'], resolve) }
        ],
        redirect: '/feedbacksFlow'
    }
]
})



